:root {
  --orange: #f8b215;
  --navy: #021d38;
}

.viewport {
  width: 100%;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #e6e6e6;
  padding: 2rem 0;
}

.initial {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: center;
  max-width: 100%;
}
.initial hgroup h1 {
  text-align: center;
  font-weight: 600;
  font-size: 2.5rem;
  margin-bottom: 1.25rem;
}
.initial hgroup p {
  font-size: 1.1rem;
  margin-bottom: 2.5rem;
  line-height: 1.8rem;
  font-weight: 500;
}
.initial hgroup span {
  background: var(--navy);
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-weight: 600;
  color: #fff;
  font-size: 1rem;
}
.initial .roulette {
  width: 500px;
}

.roulette {
  position: relative;
  border-radius: 50%;
  border: 5px solid #fff;
  -webkit-box-shadow: 2px 1px 2px 0 rgba(0, 0, 0, 0.025), 4px 2px 4px 0 rgba(0, 0, 0, 0.025), 8px 4px 8px 0 rgba(0, 0, 0, 0.05), 16px 8px 16px 0 rgba(0, 0, 0, 0.05), 32px 16px 32px 0 rgba(0, 0, 0, 0.05), 64px 32px 64px 0 rgba(0, 0, 0, 0.05);
          box-shadow: 2px 1px 2px 0 rgba(0, 0, 0, 0.025), 4px 2px 4px 0 rgba(0, 0, 0, 0.025), 8px 4px 8px 0 rgba(0, 0, 0, 0.05), 16px 8px 16px 0 rgba(0, 0, 0, 0.05), 32px 16px 32px 0 rgba(0, 0, 0, 0.05), 64px 32px 64px 0 rgba(0, 0, 0, 0.05);
}
.roulette::before {
  content: "";
  width: 0;
  height: 0;
  border-top: 0.9rem solid var(--orange);
  border-left: 0.9rem solid transparent;
  border-right: 0.9rem solid transparent;
  border-bottom: 1.8rem solid transparent;
  position: absolute;
  top: 1.8rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.roulette::after {
  content: "";
  width: 1.8rem;
  background-color: var(--orange);
  height: 1.8rem;
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.roulette canvas {
  width: 100%;
  height: 100%;
  border: 15px solid var(--navy);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 50%;
}
.roulette .roulette__btn {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.roulette .roulette__btn.isSpin button {
  background-color: var(--orange);
  color: #fff;
}
.roulette .roulette__btn.isStop button {
  background-color: var(--navy);
  color: #fff;
}
.roulette .roulette__btn.isInitial {
  -webkit-transform: translate(-50%, -50%) scale(0.6);
          transform: translate(-50%, -50%) scale(0.6);
}
.roulette .roulette__btn button {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #fff;
  -webkit-box-shadow: 2px 1px 2px 0 rgba(0, 0, 0, 0.025), 4px 2px 4px 0 rgba(0, 0, 0, 0.025), 8px 4px 8px 0 rgba(0, 0, 0, 0.05), 16px 8px 16px 0 rgba(0, 0, 0, 0.05), 32px 16px 32px 0 rgba(0, 0, 0, 0.05), 64px 32px 64px 0 rgba(0, 0, 0, 0.05);
          box-shadow: 2px 1px 2px 0 rgba(0, 0, 0, 0.025), 4px 2px 4px 0 rgba(0, 0, 0, 0.025), 8px 4px 8px 0 rgba(0, 0, 0, 0.05), 16px 8px 16px 0 rgba(0, 0, 0, 0.05), 32px 16px 32px 0 rgba(0, 0, 0, 0.05), 64px 32px 64px 0 rgba(0, 0, 0, 0.05);
  font-weight: 900;
  color: var(--navy);
}

.add-form {
  margin-top: 3rem;
  border-radius: 0.25rem;
  overflow: hidden;
}
.add-form input {
  background-color: #fff;
  width: 17rem;
  line-height: 2.5rem;
  height: 2.5rem;
  padding-left: 0.5rem;
}
.add-form button {
  width: 7rem;
  height: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
  background-color: var(--orange);
  color: #fff;
  font-weight: 600;
}

.nav-btns {
  margin-top: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.nav-btns .nav-btns__btn {
  width: 7rem;
  height: 2.5rem;
  line-height: 2.5rem;
  background-color: var(--navy);
  font-weight: 600;
  border-radius: 0.25em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.nav-btns .nav-btns__btn:not(:last-child) {
  margin-right: 0.5rem;
}
.nav-btns .nav-btns__btn p {
  color: #fff;
}
.nav-btns .nav-btns__btn span {
  display: inline-block;
  margin-right: 0.5rem;
  -webkit-transform: translateY(0.125rem);
          transform: translateY(0.125rem);
  color: #fff;
}

@media all and (max-width: 640px) {
  :root {
    font-size: 12px;
  }
  .viewport {
    padding: 2rem;
  }
  .initial hgroup p br {
    display: none;
  }
  .initial .roulette {
    width: 100%;
  }
  .roulette {
    border-width: 3px;
  }
  .roulette canvas {
    border-width: 10px;
  }
}